html {
  --blue: #00acdc;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 400;
}

body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo__img {
  width: 40px;
  height: 40px;
  margin: 10px 0 10px 50px;
  display: block;
}

.nav__item {
  text-align: center;
  display: inline-block;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  height: 60px;
  margin-right: 20px;
  font-size: 12px;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link:hover, .is-active {
  color: var(--blue);
}

.is-active {
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--blue);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}
